var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"h-full bg-background"},[_vm._ssrNode("<div class=\"px-12 mt-12\" data-v-de9674d8>","</div>",[_vm._ssrNode("<div class=\"text-white\" data-v-de9674d8>","</div>",[_vm._ssrNode("<h1 class=\"font-extrabold text-3xl md:text-4xl lg:text-5xl mb-8\" data-v-de9674d8>\n        Privacy Policy\n      </h1> "),_vm._ssrNode("<div class=\"text-gray-400 prose md:prose-lg prose-yellow\" data-v-de9674d8>","</div>",[_vm._ssrNode("<p data-v-de9674d8>This page is used to inform visitors regarding our policies with the collection, use, and disclosure of\n          Personal Information if anyone decided to use our Service.</p> <p data-v-de9674d8>If you choose to use our Service, then you agree to the collection and use of information in relation to\n          this policy. The Personal Information that we collect is used for providing and improving the Service. We\n          will not use or share your information with anyone except as described in this Privacy Policy.</p> "),_vm._ssrNode("<p data-v-de9674d8>","</p>",[_vm._ssrNode("The terms used in this Privacy Policy have the same meanings as in our Terms and Conditions, which is\n          accessible\n          "),_c('NuxtLink',{attrs:{"to":"/legal.terms-conditions"}},[_vm._v("here")]),_vm._ssrNode("\n          .\n        ")],2),_vm._ssrNode(" <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>Information Collection and Use</p> <p data-v-de9674d8>For a better experience, while using our Service, we may require you to provide us with certain personally\n          identifiable information, including but not limited to emails. The information that we request will be\n          retained by us and used as described in this privacy policy.</p> <p class=\"font-bold\" data-v-de9674d8>The app does use third party services that declare their own Terms and Conditions.\n\n          Link to Terms and Conditions of third party service providers used by the app</p> <ul data-v-de9674d8><li data-v-de9674d8><a target=\"_blank\" href=\"https://policies.google.com/terms\" class=\"underline text-gray-500\" data-v-de9674d8>Google Play Services</a></li> <li data-v-de9674d8><a target=\"_blank\" href=\"https://firebase.google.com/terms/analytics\" data-v-de9674d8>Google Analytics for Firebase</a></li> <li data-v-de9674d8><a target=\"_blank\" href=\"https://firebase.google.com/terms/crashlytics\" data-v-de9674d8>Firebase Crashlytics</a></li> <li data-v-de9674d8><a target=\"_blank\" href=\"https://onesignal.com/tos\" data-v-de9674d8>One Signal</a></li></ul> <p class=\"font-bold text-xl text-white my-6\" data-v-de9674d8>\n          Here's the breakdown of how we handle your data at Andronix\n        </p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Registering with email and password\n        </p> <p data-v-de9674d8>When you sign up using email and password on Andronix, we send your email and password to our server (we\n          use Google’s Firebase for the backend of the app). The email and password are all stored encrypted safely.\n          We do not store your emails on a separate database, which throws the possibilities to have breach in the\n          database due to poor security rules. Authentication is handled by Google’s own <a target=\"_blank\" href=\"https://firebase.google.com/products/auth\" data-v-de9674d8>Firebase Authentication</a>. This\n          also limits the possibility of us selling the actual data or in general spamming emails with promotional\n          content.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Google sign-in/register\n        </p> <p data-v-de9674d8>When using Google sign-in, the entire OAuth process is handled by <a target=\"_blank\" href=\"https://firebase.google.com/products/auth\" data-v-de9674d8>Firebase Authentication</a>. We only have\n          access to your email and your profile picture and nothing else. We neither store your email or your profile\n          picture on a separate database. Your picture is fetched in real-time using Google’s APIs.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          In-app purchases\n        </p> <p data-v-de9674d8>We always store the minimum amount of data that is enough to identify you as our user but can not be used\n          to identify you with any metadata by a third party. Note - This is only applicable to the systems that we\n          operate and have under control and does not include any of the external services that we use or might use in\n          the future. As an example, if you use a Google service and have a Google account prior to using Andronix,\n          there's a good chance that Google might track you inside Andronix as well with all the Firebase products\n          that we use.</p> <p class=\"font-bold\" data-v-de9674d8>In a nutshell, the fore-mentioned policies of handling your data is only applicable\n          within our systems. This cannot personally identify you as an individual unless your email is associated\n          elsewhere.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Purchases made outside the app\n        </p> <p data-v-de9674d8>Andronix currently uses <a href=\"https://razorpay.com\" data-v-de9674d8>Razorpay</a> as the payment provider along with <a target=\"_blank\" href=\"https://paypal.com\" data-v-de9674d8>PayPal</a> which is actually integrated within Razorpay. The present purchase flow requires users to add the\n          following details as &quot;Billing Information&quot;</p> <ul data-v-de9674d8><li data-v-de9674d8>First Name</li> <li data-v-de9674d8>Last Name</li> <li data-v-de9674d8>Billing address</li> <li data-v-de9674d8>Postal code</li> <li data-v-de9674d8>Country</li> <li data-v-de9674d8>State</li> <li data-v-de9674d8>Email</li></ul> <p class=\"prose-sm\" data-v-de9674d8>* This list might change in the future. This is helpful in preventing credit card fraud\n          and other types of fraud. It also mandatory in most of the States in the world.</p> <p data-v-de9674d8>This entire data is stored on Razorpay's backend and our servers never touch this. This is also how your\n          payments are handled, we never see them or make one enter their financial information on your website.</p> <p class=\"font-bold\" data-v-de9674d8>In a nutshell, the fore-mentioned policies of handling your data is only applicable\n          within our systems. This cannot personally identify you as an individual unless your email is associated\n          elsewhere.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Use of Analytics and Crashlytics</p> <p data-v-de9674d8>We use Google Analytics that comprises of Crashlytics. Analytics enables us to take a look at what users\n          are using the most in the app and improve the UX of the app. Whereas Crashlytics helps us to identify the\n          issues and crashes automatically before a user manually reports the crashes.</p> <p class=\"font-bold\" data-v-de9674d8>We don’t use the meta data is anyway other than improving the user-experience. It is not\n          stored on our servers.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Log Data</p> <p data-v-de9674d8></p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8></p> <p data-v-de9674d8>We want to inform you that whenever you use our Service, in a case of an error in the app we collect data\n          and information (through third party products) on your phone called Log Data. This Log Data may include\n          information such as your device Internet Protocol (“IP”) address, device name, operating system version, the\n          configuration of the app when utilizing our Service, the time and date of your use of the Service, and other\n          statistics.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Cookies</p> <p data-v-de9674d8>Cookies are files with a small amount of data that are commonly used as anonymous unique identifiers. These\n          are sent to your browser from the websites that you visit and are stored on your device’s internal\n          memory.</p> <p data-v-de9674d8>This Service does not use these “cookies” explicitly. However, the app may use third party code and\n          libraries that use “cookies” to collect information and improve their services. You have the option to\n          either accept or refuse these cookies and know when a cookie is being sent to your device. If you choose to\n          refuse our cookies, you may not be able to use some portions of this Service.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Service Providers</p> <p data-v-de9674d8>We may employ third-party companies and individuals due to the following reasons:</p> <ul data-v-de9674d8><li data-v-de9674d8>To facilitate our Service;</li> <li data-v-de9674d8>To provide the Service on our behalf;</li> <li data-v-de9674d8>To perform Service-related services; or</li> <li data-v-de9674d8>To assist us in analysing how our Service is used.</li></ul> <p data-v-de9674d8>We want to inform users of this Service that these third parties have access to your Personal Information.\n          The reason is to perform the tasks assigned to them on our behalf. However, they are obligated not to\n          disclose or use the information for any other purpose.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Security</p> <p data-v-de9674d8>We value your trust in providing us your Personal Information, thus we are striving to use commercially\n          acceptable means of protecting it. But remember that no method of transmission over the internet, or method\n          of electronic storage is 100% secure and reliable, and we cannot guarantee its absolute security.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Links to Other Sites </p> <p data-v-de9674d8>This Service may contain links to other sites. If you click on a third-party link, you will be directed to\n          that site. Note that these external sites are not operated by us. Therefore, we strongly advise you to\n          review the Privacy Policy of these websites. We have no control over and assume no responsibility for the\n          content, privacy policies, or practices of any third-party sites or services.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>\n          Children’s Privacy</p> <p data-v-de9674d8>These Services do not address anyone under the age of 13. We do not knowingly collect personally\n          identifiable information from children under 13. In the case we discover that a child under 13 has provided\n          us with personal information, we immediately delete this from our servers. If you are a parent or guardian\n          and you are aware that your child has provided us with personal information, please contact us so that we\n          will be able to do necessary actions.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>Changes to This Privacy Policy</p> <p data-v-de9674d8>We may update our Terms and Conditions from time to time. Thus, you are advised to review this page\n          periodically for any changes. We will notify you of any changes by posting the new Terms and Conditions on\n          this page. These changes are effective immediately after they are posted on this page.</p> <p class=\"font-bold text-lg text-white my-3\" data-v-de9674d8>Contact Us</p> <p data-v-de9674d8>If you have any questions or suggestions about our Privacy Policy, do not hesitate to contact us at <a href=\"mailto:andronix@techriz.com\" data-v-de9674d8>andronix@techriz.com</a></p>")],2)],2)])])
